// front-page

header {
  display: flex;
  justify-content: center;
  width: 100%;
}

h2 {

  text-transform: uppercase;
}

.home {
  .site-header {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
  }

  .main-navigation a {
    color: white;
  }
  
  .main-navigation a:hover {
    color: ivory;
  }

  //banner - Hero Image
  .banner-image {
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url(../.././assets/images/home-hero.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    height: 1000px;
    width: 100vw;
    margin: 0;

    .white-circle-logo {
      position: absolute;
      top: 31%;
      left: 40%;
    }
  }

  // shop-stuff
  .shop-stuff {
    justify-content: space-between;
    text-align: center;
    ul {
      display: flex;
      flex-direction: row;
      justify-content: center;
      list-style: none;

      li {
        border: 1px solid #e1e1e1;
        border-top-width: 5px;
        padding: 1rem;
        margin: 10px;
        img {
          max-width: 6vw;
        }
        a {
          border: 1px solid #e1e1e1;
          background: #248a83;
          color: white;
        }
      }
    }
  }

  //inhabitent-journal

  .journal-section {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-left: auto;
    margin-right: auto;
    .journal-entry {
      // display: flex;
      // flex: wrap;
      // flex-direction: column;
      padding: 20px;
      border: 1px solid #e1e1e1;
      margin: 10px;
      img {
        height: 30vh;
      }
      .read-entry {
        border: 1px solid #e1e1e1;
      }
      .read-entry:hover {
        background: #248a83;
        color: white;
      }
    }
  }

  //latest-adventures

  .adventures {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(400px, auto);
    grid-template-rows: repeat(2, 300px);
    grid-gap: 10px;
    a {
      border: 1px solid #e1e1e1;
    }
    h3 {
      text-transform: capitalize;
      padding-left: 20px;
      color: white;
    }

    p {
      text-decoration: none;
    }

    .read-more {
      margin-left: 20px;
      width: 130px;
      padding: 0.75rem;
      background: none;
      text-transform: uppercase;
      color: #ffffff;
      border: 1px solid white;
    }

    .adventures-1 {
      background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        url(../.././assets/images/adventure-photos/canoe-girl.jpg);
      background-position: center;
      background-size: cover;
      grid-column: 1/3;
      grid-row: 1/3;
      border: 4px solid white;
      // margin-left: 20px;
    }

    .adventures-2 {
      background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        url(../.././assets/images/adventure-photos/beach-bonfire.jpg);
      background-position: center;
      background-size: cover;
      grid-column: 3/5;
      grid-row: 1/2;
      border: 4px solid white;
      margin-right: 20px;
    }

    .adventures-3 {
      background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        url(../.././assets/images/adventure-photos/mountain-hikers.jpg);
      background-position: center;
      background-size: cover;
      grid-column: 3;
      grid-row: 2/2;
      border: 4px solid white;
    }

    .adventures-4 {
      background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        url(../.././assets/images/adventure-photos/night-sky.jpg);
      background-position: center;
      background-size: cover;
      grid-column: 4;
      grid-row: 2/2;
      border: 4px solid white;
      margin-right: 20px;
    }
  }
  .adventure-button {
    margin-top: 2rem;
    .more-adventure {
      background: #248a83;
      text-transform: uppercase;
      color: white;
      padding: 1rem;
      margin-top: 20px;
      margin-bottom: 20px;
      font-size: 1rem;
    }
  }

  .entry-footer {
    background: none;
    color: #fff;
  }

  .title-meta-background {
    .feature-image {
      position: relative;

    }
    .post-title {
      position: absolute;

      background: #248a83;
      color: white;
    }
  }
}
