.post-type-archive-product {
  .logos {
    .site-logo {
      display: unset;
    }
    .site-logo-white {
      display: none;
    }
  }
  .page-header {
    display: unset;
    .page-title {
      text-align: center;
    }
    .tax-description {
      margin: 0;
    }
  }
  .content-area {
    width: 100%;
    border: 0;

    .product-type-nav {
      display: flex;
      flex-direction: row;
      justify-content: center;
      border-bottom: 1px dashed #c8c8c8;
      a {
        font-size: 1.2rem;
        margin-right: 1.4rem;
        text-transform: uppercase;
      }
    }

    .product-type {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(4, 1fr);
      grid-gap: 1.5rem;
      // margin-bottom: 1.5rem;
      margin-top: 3rem;
    }

    img {
      height: 260px;
      width: 100%;
      object-fit: cover;
      border-bottom: 4px solid #e1e1e1;
    }

    .product-wrapper {
      border: 1px solid #e1e1e1;
      margin-bottom: 0;
      .type-product {
        margin-bottom: 0;
      }
      .product-info {
        width: 260px;
        display: flex;
        padding: 1rem;
        justify-content: space-between;
      }

      .title {
        white-space: nowrap;
      }

      .dots {
        flex-grow: 1;
        overflow: hidden;
        &::after {
          content: '.........................................................';
        }
      }
    }
  }
}
