/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/

/* About */

.page-about {
  header {
    position: absolute;
  }

  .hero-image-header {
    height: 900px;
    width: 100vw;
    margin-top: 0;
    position: relative;
    .entry-title {
      color: #ffffff;
    }
  }
  .entry-content {
    margin-top: 0;
    p {
      width: 1120px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

// 404
.error404 {
  .logos {
    .site-logo {
      display: unset;
    }
    .site-logo-white {
      display: none;
    }
  }
  ul {
    list-style: none;
    margin-left: 0;
  }
}

//search
.search {
  .logos {
    .site-logo {
      display: unset;
    }
    .site-logo-white {
      display: none;
    }
  }
  header {
    justify-content: left;
  }

  .journal-readmore {
    border: 1px solid #000;
    color: #000;
    padding: 0.75rem 1.5rem;
    text-transform: uppercase;
    font-size: 0.9rem;
    margin-bottom: 2rem;
    width: 160px;
    a {
      color: #000;
    }
  }
  .journal-readmore:hover {
    background: #248a83;
    border: none;
    color: #ffffff;
    a {
      color: #ffffff;
    }
  }

  .page-numbers {
    background: #248a83;
    color: #ffffff;
    font-family: 'Novecento Sans Web', sans-serif;
    padding: 0.5rem 0.75rem;
  }
  .page-numbers:hover {
    background: rgb(18, 18, 83);
  }
  .current {
    background: gray;
  }
}



// adventure 
.type-page {
.hero-image-header {
  height: 1000px;
}
p {
  max-width: 1240px;
  margin-right: auto;
  margin-left: auto;
}
}
